/** @flow */
import PropTypes from 'prop-types'
import React, { Component } from 'react'
import { createPortal } from 'react-dom'
import Select from 'react-select'

// Import directly to avoid Webpack bundling the parts of react-virtualized that we are not using
import AutoSizer from 'react-virtualized/dist/commonjs/AutoSizer'
import List from 'react-virtualized/dist/commonjs/List'

const styleFromSelectEl = el => {
  if (!el) return {}
  const rect = el.getBoundingClientRect()
  return {
    overflow: 'hidden',
    width: `${rect.width}px`,
    left: `${Math.trunc(rect.left)}px`,
    top: `${rect.top + rect.height}px`,
    position: 'absolute'
  }
}
const REGEXP_SCROLL_PARENT = /^(visible|hidden)/
const getScrollParents = (el) => {
  const list = []
  const _recurseParents = el => {
    if (!el || typeof window.getComputedStyle !== 'function') {
      return null
    }
    if (el.scrollHeight >= el.clientHeight && !REGEXP_SCROLL_PARENT.test(window.getComputedStyle(el).overflowY || 'visible')) {
      list.push(el)
    }
    return _recurseParents(el.parentElement) || document.body
  }

  _recurseParents(el)
  return list
}
const PortalContainer = ({selectRef, menuPortalProps, children}) => {
  const [style, setStyle] = React.useState(() => styleFromSelectEl(selectRef.control))

  React.useEffect(() => {
    const scrollParents = getScrollParents(selectRef.control)
    const update = () => {
      setStyle(styleFromSelectEl(selectRef.control))
    }
    const timer = setInterval(update, 100)

    scrollParents.forEach(p => p.addEventListener('scroll', update))
    return () => {
      clearInterval(timer)
      scrollParents.forEach(p => p.removeEventListener('scroll', update))
    }
  }, [])

  return (
    <div style={style} className='VirtualizedSelectMenuPortal' {...menuPortalProps}>
      {children}
    </div>
  )
}

export default class VirtualizedSelect extends Component {

  static propTypes = {
    async: PropTypes.bool,
    renderToPortal: PropTypes.bool,
    listProps: PropTypes.object,
    menuPortalProps: PropTypes.object,
    maxHeight: PropTypes.number,
    optionHeight: PropTypes.oneOfType([PropTypes.number, PropTypes.func]),
    optionRenderer: PropTypes.func,
    selectComponent: PropTypes.func
  };

  static defaultProps = {
    async: false,
    maxHeight: 200,
    optionHeight: 35
  };

  constructor (props, context) {
    super(props, context)

    this._renderMenu = this._renderMenu.bind(this)
    this._optionRenderer = this._optionRenderer.bind(this)
    this._setListRef = this._setListRef.bind(this)
    this._setSelectRef = this._setSelectRef.bind(this)
  }

  componentWillUnmount () {
    if (this._portalEl) {
      document.body.removeChild(this._portalEl)
    }
  }

  /** See List#recomputeRowHeights */
  recomputeOptionHeights (index = 0) {
    if (this._listRef) {
      this._listRef.recomputeRowHeights(index)
    }
  }

  /** See Select#focus (in react-select) */
  focus () {
    if (this._selectRef) {
      return this._selectRef.focus()
    }
  }

  render () {
    const SelectComponent = this._getSelectComponent()

    return (
      <SelectComponent
        {...this.props}
        ref={this._setSelectRef}
        menuRenderer={this._renderMenu}
        menuStyle={{ overflow: 'hidden' }}
      />
    )
  }

  // See https://github.com/JedWatson/react-select/#effeciently-rendering-large-lists-with-windowing
  _renderMenu ({ focusedOption, focusOption, labelKey, onSelect, options, selectValue, valueArray, valueKey }) {
    const { renderToPortal, listProps, optionRenderer, menuPortalProps = {} } = this.props
    const focusedOptionIndex = options.indexOf(focusedOption)
    const height = this._calculateListHeight({ options })
    const innerRowRenderer = optionRenderer || this._optionRenderer

    // react-select 1.0.0-rc2 passes duplicate `onSelect` and `selectValue` props to `menuRenderer`
    // The `Creatable` HOC only overrides `onSelect` which breaks an edge-case
    // In order to support creating items via clicking on the placeholder option,
    // We need to ensure that the specified `onSelect` handle is the one we use.
    // See issue #33

    function wrappedRowRenderer ({ index, key, style }) {
      const option = options[index]

      return innerRowRenderer({
        focusedOption,
        focusedOptionIndex,
        focusOption,
        key,
        labelKey,
        onSelect,
        option,
        optionIndex: index,
        options,
        selectValue: onSelect,
        style,
        valueArray,
        valueKey
      })
    }

    const contents = (
      <AutoSizer disableHeight>
        {({ width }) => (
          <List
            className='VirtualSelectGrid'
            height={height}
            ref={this._setListRef}
            rowCount={options.length}
            rowHeight={({ index }) => this._getOptionHeight({
              option: options[index]
            })}
            rowRenderer={wrappedRowRenderer}
            scrollToIndex={focusedOptionIndex}
            width={width}
            {...listProps}
          />
        )}
      </AutoSizer>
    )

    if (renderToPortal) {
      if (!this._portalEl) {
        this._portalEl = document.createElement('div')
        document.body.appendChild(this._portalEl)
      }

      return createPortal((
        <PortalContainer menuPortalProps={menuPortalProps} selectRef={this._selectRef.select || this._selectRef}>
          {contents}
        </PortalContainer>
      ), this._portalEl)
    }
    return contents
  }

  _calculateListHeight ({ options }) {
    const { maxHeight } = this.props

    let height = 0

    for (let optionIndex = 0; optionIndex < options.length; optionIndex++) {
      let option = options[optionIndex]

      height += this._getOptionHeight({ option })

      if (height > maxHeight) {
        return maxHeight
      }
    }

    return height
  }

  _getOptionHeight ({ option }) {
    const { optionHeight } = this.props

    return optionHeight instanceof Function
      ? optionHeight({ option })
      : optionHeight
  }

  _getSelectComponent () {
    const { async, selectComponent } = this.props

    if (selectComponent) {
      return selectComponent
    } else if (async) {
      return Select.Async
    } else {
      return Select
    }
  }

  _optionRenderer ({ focusedOption, focusOption, key, labelKey, option, selectValue, style, valueArray }) {
    const className = ['VirtualizedSelectOption']

    if (option === focusedOption) {
      className.push('VirtualizedSelectFocusedOption')
    }

    if (option.disabled) {
      className.push('VirtualizedSelectDisabledOption')
    }

    if (valueArray && valueArray.indexOf(option) >= 0) {
      className.push('VirtualizedSelectSelectedOption')
    }

    if (option.className) {
      className.push(option.className)
    }

    const events = option.disabled
      ? {}
      : {
        onClick: () => selectValue(option),
        onMouseEnter: () => focusOption(option)
      }

    return (
      <div
        className={className.join(' ')}
        key={key}
        style={style}
        title={option.title}
        {...events}
      >
        {option[labelKey]}
      </div>
    )
  }

  _setListRef (ref) {
    this._listRef = ref
  }

  _setSelectRef (ref) {
    this._selectRef = ref
  }
}



